import React, { useRef } from 'react';
import PropTypes from 'prop-types';
import { useReactToPrint } from 'react-to-print';

export const GroupSheet = ({ group, players, courseColumns, tournamentName }) => {
  const componentRef = useRef();

  const handlePrint = useReactToPrint({
    content: () => componentRef.current,
  });

  const tableStyle = {
    width: '100%',
    borderCollapse: 'collapse',
    border: '2px solid #000',
    fontSize: '10px',
    pageBreakInside: 'avoid',
    tableLayout: 'fixed'
  };

  const cellStyle = {
    border: '1px solid #000',
    padding: '2.1px 1px',  
    textAlign: 'center',
    height: '26.25px',  
    fontSize: '11px',  
    borderWidth: '1px',
    overflow: 'hidden',
    whiteSpace: 'nowrap'
  };

  const headerCellStyle = {
    ...cellStyle,
    backgroundColor: '#f0f0f0',
    fontWeight: 'bold'
  };

  const skyBlueHeaderStyle = {
    ...headerCellStyle,
    backgroundColor: '#e3f2fd',
    fontSize: '14px',
    height: '33.6px'
  };

  const parCellStyle = {
    ...cellStyle,
    color: '#000',
    backgroundColor: '#f9f9f9',
    width: '35px'
  };

  const holeCellStyle = {
    ...headerCellStyle,
    width: '25px'
  };

  const parNumberStyle = {
    fontSize: '13px',
    fontWeight: 'bold',
    lineHeight: '1.2'
  };

  const parDistanceStyle = {
    fontSize: '10px',
    color: '#666',
    marginTop: '1px'
  };

  const scoreCellStyle = {
    ...cellStyle,
    backgroundColor: '#fff',
    width: '45px'
  };

  const chineseCharStyle = {
    ...cellStyle,
    color: '#888',
    fontFamily: 'serif',
    fontSize: '12px',
    width: '20px'
  };

  const chineseHeaderStyle = {
    ...headerCellStyle,
    fontFamily: 'serif',
    fontSize: '12px',
    width: '20px'
  };

  const getParInfo = (courseName, holeNumber) => {
    const parData = {
      'A': [
        { par: 3, distance: 55 },
        { par: 4, distance: 67 },
        { par: 4, distance: 75 },
        { par: 4, distance: 70 },
        { par: 3, distance: 58 },
        { par: 5, distance: 130 },
        { par: 4, distance: 70 },
        { par: 3, distance: 52 },
        { par: 3, distance: 55 }
      ],
      'B': [
        { par: 4, distance: 75 },
        { par: 3, distance: 52 },
        { par: 3, distance: 56 },
        { par: 3, distance: 47 },
        { par: 5, distance: 132 },
        { par: 4, distance: 88 },
        { par: 4, distance: 74 },
        { par: 3, distance: 55 },
        { par: 4, distance: 76 }
      ],
      'C': [
        { par: 4, distance: 67 },
        { par: 5, distance: 110 },
        { par: 4, distance: 65 },
        { par: 3, distance: 42 },
        { par: 3, distance: 43 },
        { par: 4, distance: 65 },
        { par: 3, distance: 57 },
        { par: 4, distance: 68 },
        { par: 3, distance: 56 }
      ],
      'D': [
        { par: 4, distance: 75 },
        { par: 4, distance: 87 },
        { par: 3, distance: 41 },
        { par: 4, distance: 70 },
        { par: 5, distance: 102 },
        { par: 3, distance: 55 },
        { par: 3, distance: 59 },
        { par: 4, distance: 70 },
        { par: 3, distance: 48 }
      ],
      'E': [
        { par: 4, distance: 61 },
        { par: 5, distance: 123 },
        { par: 3, distance: 43 },
        { par: 4, distance: 65 },
        { par: 3, distance: 48 },
        { par: 3, distance: 47 },
        { par: 4, distance: 70 },
        { par: 3, distance: 54 },
        { par: 4, distance: 74 }
      ]
    };

    return parData[courseName]?.[holeNumber - 1] || { par: '', distance: '' };
  };

  const renderCourseTable = (courseName, coursePlayers) => {
    const totalCols = 2 + (coursePlayers.length * 2);

    return (
      <table style={tableStyle}>
        <thead>
          <tr>
            <th colSpan={totalCols} style={skyBlueHeaderStyle} className="course-title">
              {group} - {courseName} (,)
            </th>
          </tr>
          <tr>
            <th colSpan="2" style={skyBlueHeaderStyle}></th>
            {coursePlayers.map((player, idx) => (
              <th key={idx} style={headerCellStyle} colSpan="2" className="player-name">
                {player.name || ` ${idx + 1}`}
              </th>
            ))}
          </tr>
          <tr>
            <th style={holeCellStyle}></th>
            <th style={parCellStyle}></th>
            {coursePlayers.map((_, idx) => (
              <React.Fragment key={idx}>
                <th style={chineseHeaderStyle}></th>
                <th style={headerCellStyle}></th>
              </React.Fragment>
            ))}
          </tr>
        </thead>
        <tbody>
          {[...Array(9)].map((_, i) => {
            const parInfo = getParInfo(courseName, i + 1);
            return (
              <tr key={i}>
                <td style={holeCellStyle}>{i + 1}</td>
                <td style={parCellStyle}>
                  <div style={parNumberStyle}>{parInfo.par}</div>
                  <div style={parDistanceStyle}>{parInfo.distance}M</div>
                </td>
                {coursePlayers.map((_, playerIdx) => (
                  <React.Fragment key={playerIdx}>
                    <td style={chineseCharStyle}></td>
                    <td style={scoreCellStyle}></td>
                  </React.Fragment>
                ))}
              </tr>
            );
          })}
          {/*   */}
          <tr>
            <td style={{...headerCellStyle, height: '36.75px'}} colSpan="2"></td>
            {coursePlayers.map((_, playerIdx) => (
              <td key={playerIdx} style={{...scoreCellStyle, height: '36.75px'}} colSpan="2"></td>
            ))}
          </tr>
          {/*   */}
          <tr>
            <td style={{...headerCellStyle, height: '42px'}} colSpan="2"></td>
            {coursePlayers.map((_, playerIdx) => (
              <td key={playerIdx} style={{...scoreCellStyle, height: '42px'}} colSpan="2"></td>
            ))}
          </tr>
        </tbody>
      </table>
    );
  };

  const uniqueCourses = courseColumns
    .filter((col, index, self) => 
      index === self.findIndex(t => t.split('_')[0] === col.split('_')[0])
    )
    .map(col => col.split('_')[0] + '');

  return (
    <>
      <button 
        onClick={handlePrint}
        style={{
          position: 'fixed',
          top: '10px',
          right: '10px',
          zIndex: 1000,
          padding: '8px 16px',
          backgroundColor: '#007bff',
          color: 'white',
          border: 'none',
          borderRadius: '4px',
          cursor: 'pointer'
        }}
      >
        
      </button>
      <div 
        ref={componentRef}
        style={{ 
          width: '210mm',
          minHeight: '297mm',
          margin: '0 auto',
          background: 'white',
          padding: '1mm',
          position: 'relative'
        }}
      >
        <style type="text/css" media="print">
          {`
            @page {
              size: A4 portrait;
              margin: 0;
            }
            @media print {
              html, body {
                width: 210mm;
                height: 297mm;
                margin: 0;
                padding: 0;
              }
              .score-card {
                page-break-inside: avoid;
                break-inside: avoid;
              }
            }
          `}
        </style>
        <div style={{
          display: 'grid',
          gridTemplateColumns: 'repeat(2, 1fr)',
          gap: '2mm',
          height: '100%',
          maxWidth: '208mm',
          margin: '0 auto'
        }}>
          {uniqueCourses.map((courseName, idx) => (
            <div 
              key={idx} 
              className="score-card"
              style={{
                width: '100%',
                margin: '0 auto',
                marginBottom: '8.4mm'
              }}
            >
              <div style={{
                textAlign: 'center',
                marginBottom: '2.1mm',
                fontSize: '14px',
                fontWeight: 'bold'
              }}>
                {tournamentName}
              </div>
              {renderCourseTable(courseName, players)}
            </div>
          ))}
        </div>
      </div>
    </>
  );
};

GroupSheet.propTypes = {
  group: PropTypes.string.isRequired,
  players: PropTypes.arrayOf(
    PropTypes.shape({
      name: PropTypes.string.isRequired
    })
  ).isRequired,
  courseColumns: PropTypes.arrayOf(PropTypes.string).isRequired,
  tournamentName: PropTypes.string.isRequired
}; 