import React from 'react';
import PropTypes from 'prop-types';
import styles from '../styles/styles';

export const TableHeader = ({ courseColumns, onSort, isPrintMode }) => {
  //   
  const getDayColor = (columnName) => {
    const dayMatch = columnName.match(/_(\d+)/);
    if (!dayMatch) return styles.th.backgroundColor;

    const dayNumber = parseInt(dayMatch[1]);
    const colors = [
      '#f1f1f1',  // 1:  
      '#e3f2fd',  // 2:  
      '#f1f8e9',  // 3:  
      '#fff3e0',  // 4:  
      '#f3e5f5',  // 5:  
      '#e0f7fa',  // 6:  
      '#fce4ec',  // 7:  
    ];
    
    return colors[(dayNumber - 1) % colors.length];
  };

  return (
    <thead>
      <tr>
        {!isPrintMode && (
          <th className="action-column" style={{ ...styles.th, width: '24px' }}></th>
        )}
        <th style={{ ...styles.th, width: '40px' }} onClick={() => onSort('')}>
          
        </th>
        <th style={{ ...styles.th, width: '60px' }} onClick={() => onSort('/')}>
          /
        </th>
        <th style={{ ...styles.th, width: '60px' }} onClick={() => onSort('')}>
          
        </th>
        <th style={{ ...styles.th, width: '60px' }} onClick={() => onSort('')}>
          
        </th>
        {courseColumns.map((c, i) => (
          <th 
            key={i} 
            style={{ 
              ...styles.th, 
              backgroundColor: getDayColor(c),
              borderRight: c.match(/_\d+/) && 
                (i === courseColumns.length - 1 || !courseColumns[i + 1].includes(`_${c.match(/_(\d+)/)[1]}`))
                ? '2px solid #ddd' 
                : '1px solid #ddd'
            }} 
            onClick={() => onSort(c)}
          >
            {c.replace(/_(\d+) $/, '_$1')}
          </th>
        ))}
        <th style={{ ...styles.th, backgroundColor: '#f8f9fa' }} onClick={() => onSort(' ')}>
           
        </th>
        <th style={{ ...styles.th, backgroundColor: '#f8f9fa' }} onClick={() => onSort('')}>
          
        </th>
      </tr>
    </thead>
  );
};

TableHeader.propTypes = {
  courseColumns: PropTypes.arrayOf(PropTypes.string).isRequired,
  onSort: PropTypes.func.isRequired,
  isPrintMode: PropTypes.bool
}; 