import React from 'react';
import PropTypes from 'prop-types';

const cardStyles = {
  container: {
    width: '297mm',  // A4 height
    height: '210mm',  // A4 width
    padding: '5mm',
    backgroundColor: 'white',
    boxSizing: 'border-box',
    transform: 'rotate(90deg)',
    transformOrigin: 'top left',
    position: 'absolute',
    left: '0',
    top: '297mm',
  },
  cardGrid: {
    display: 'grid',
    gridTemplateColumns: 'repeat(3, 58mm)',  // 5.8cm per card height
    gridTemplateRows: 'repeat(3, 95mm)',     // 9.5cm per card width
    gap: '5mm',
    height: '100%',
  },
  card: {
    border: '1px solid #000',
    padding: '5mm',
    display: 'flex',
    flexDirection: 'column',
    height: '58mm',   // 5.8cm height
    width: '95mm',    // 9.5cm width
    boxSizing: 'border-box',
    backgroundColor: '#fff',
    transform: 'rotate(-90deg)',  //    
  },
  header: {
    marginBottom: '3mm',
    padding: '2mm 0',
    textAlign: 'center',
    fontSize: '16px',
    fontWeight: 'bold',
    borderBottom: '1px solid #000',
  },
  content: {
    display: 'grid',
    gridTemplateColumns: '1fr 1fr',
    gridTemplateRows: '1fr 1fr',
    gap: '1mm',
    border: '1px solid #000',
    flex: 1,
  },
  cell: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    padding: '1mm',
    borderRight: '1px solid #000',
    borderBottom: '1px solid #000',
    fontSize: '14px',
    fontWeight: 'bold',
  },
  lastColumnCell: {
    borderRight: 'none',
  },
  lastRowCell: {
    borderBottom: 'none',
  },
  nameCell: {
    fontSize: '20px',
  }
};

export const BusinessCardSheet = ({ rows, tournamentName }) => {
  return (
    <div style={{ width: '210mm', height: '297mm', position: 'relative', overflow: 'hidden' }}>
      <div style={cardStyles.container}>
        <div style={cardStyles.cardGrid}>
          {rows.map((row, index) => (
            <div key={index} style={cardStyles.card}>
              <div style={cardStyles.header}>
                {tournamentName}
              </div>
              <div style={cardStyles.content}>
                <div style={cardStyles.cell}>
                  {row.group}
                </div>
                <div style={{...cardStyles.cell, ...cardStyles.lastColumnCell}}>
                  {row.region}
                </div>
                <div style={{...cardStyles.cell, ...cardStyles.lastRowCell}}>
                  {row.startCourse}
                </div>
                <div style={{
                  ...cardStyles.cell,
                  ...cardStyles.lastColumnCell,
                  ...cardStyles.lastRowCell,
                  ...cardStyles.nameCell
                }}>
                  {row.name}
                </div>
              </div>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

BusinessCardSheet.propTypes = {
  rows: PropTypes.arrayOf(
    PropTypes.shape({
      group: PropTypes.string,
      name: PropTypes.string,
      region: PropTypes.string,
      startCourse: PropTypes.string,
    })
  ).isRequired,
  tournamentName: PropTypes.string.isRequired,
}; 