import { saveToFile, loadFromFile } from '../utils/fileHandlers';
import html2canvas from 'html2canvas';
import React from 'react';
import ReactDOM from 'react-dom';
import { GroupSheet } from '../components/GroupSheet';

export const useTableEvents = ({
  rows,
  setRows,
  courseColumns,
  setCourseColumns,
  tournamentName,
  setTournamentName,
  totalDays,
  setTotalDays
}) => {
  const handleFileLoad = async (event) => {
    const file = event.target.files[0];
    if (!file) return;

    try {
      const data = await loadFromFile(file);
      setRows(data.rows || []);
      setCourseColumns(data.courseColumns || []);
      setTournamentName(data.tournamentName || '');
      
      // totalDays 
      const maxDay = Math.max(...data.courseColumns.map(col => {
        const match = col.match(/_(\d+)/);
        return match ? parseInt(match[1]) : 1;
      }));
      setTotalDays(maxDay || 1);
    } catch (error) {
      console.error('    :', error);
      alert('    .');
    }
  };

  const handleSaveFile = () => {
    const data = {
      tournamentName,
      courseColumns,
      rows,
      totalDays
    };
    saveToFile(data, `${tournamentName || ''}.json`);
  };

  const handleCaptureTable = async () => {
    try {
      //     
      const printArea = document.getElementById('printArea');
      const originalStyle = printArea.style.cssText;
      
      //    
      printArea.style.cssText = `
        margin: 0 !important;
        padding: 10px !important;
        width: 100% !important;
        background-color: white !important;
        display: flex !important;
        justify-content: center !important;
      `;

      //      
      const table = printArea.querySelector('table');
      const originalTableStyle = table.style.cssText;
      table.style.cssText = `
        width: 98% !important;
        margin: 0 auto !important;
        table-layout: fixed !important;
      `;

      //   
      const actionColumns = document.querySelectorAll('.action-column');
      actionColumns.forEach(col => {
        col.style.display = 'none';
      });

      const canvas = await html2canvas(printArea, {
        scale: 2, //  2 
        useCORS: true,
        backgroundColor: '#ffffff',
        width: printArea.scrollWidth,
        height: printArea.scrollHeight,
        windowWidth: printArea.scrollWidth,
        windowHeight: printArea.scrollHeight
      });

      //   
      printArea.style.cssText = originalStyle;
      table.style.cssText = originalTableStyle;
      actionColumns.forEach(col => {
        col.style.display = '';
      });

      //  
      const link = document.createElement('a');
      link.download = `${tournamentName || ''}_.png`;
      link.href = canvas.toDataURL('image/png');
      link.click();
    } catch (error) {
      console.error('    :', error);
      alert('    .');
    }
  };

  const handleCaptureGroupSheet = async () => {
    //   
    const groupedPlayers = rows.reduce((acc, player) => {
      const group = player.group || '';
      if (!acc[group]) {
        acc[group] = [];
      }
      acc[group].push(player);
      return acc;
    }, {});

    //   
    const newTab = window.open('', '_blank');
    newTab.document.write(`
      <html>
        <head>
          <meta charset="utf-8">
          <title>${tournamentName} -  </title>
          <style>
            @page {
              size: A4 portrait;
              margin: 0;
            }
            html {
              background: #eee;
            }
            body {
              margin: 0;
              padding: 0;
              background: #eee;
            }
            .page {
              width: 210mm;
              height: 297mm;
              padding: 10mm;
              margin: 10mm auto;
              background: white;
              box-sizing: border-box;
              page-break-after: always;
              display: flex;
              justify-content: center;
              align-items: center;
            }
            .page:last-child {
              page-break-after: auto;
            }
            @media print {
              html, body {
                background: white;
                height: auto;
              }
              .page {
                margin: 0;
                padding: 10mm;
                height: 297mm;
                page-break-after: always;
              }
              .page:last-child {
                page-break-after: auto;
              }
            }
          </style>
        </head>
        <body>
    `);

    //   
    const container = document.createElement('div');
    container.style.cssText = `
      position: fixed;
      left: -9999px;
      top: 0;
      width: 210mm;
      height: 297mm;
      background: white;
      padding: 0;
    `;
    document.body.appendChild(container);

    try {
      //    
      for (const [group, players] of Object.entries(groupedPlayers)) {
        // GroupSheet 
        ReactDOM.render(
          <GroupSheet
            group={group}
            players={players}
            courseColumns={courseColumns}
            tournamentName={tournamentName}
          />,
          container
        );

        //     
        await new Promise(resolve => setTimeout(resolve, 100));

        //  
        const canvas = await html2canvas(container, {
          scale: 2,
          useCORS: true,
          logging: false,
          width: container.offsetWidth,
          height: container.offsetHeight,
          windowWidth: 210 * 3.78,  // mm to px
          windowHeight: 297 * 3.78
        });

        //    
        const image = canvas.toDataURL('image/png', 1.0);
        newTab.document.write(`
          <div class="page">
            <img src="${image}" style="width: 100%; height: auto;">
          </div>
        `);
      }

      // HTML  
      newTab.document.write('</body></html>');
      newTab.document.close();
      newTab.focus();
    } catch (error) {
      console.error('     :', error);
      alert('     .');
    } finally {
      //   
      ReactDOM.unmountComponentAtNode(container);
      document.body.removeChild(container);
    }
  };

  return {
    handleFileLoad,
    handleSaveFile,
    handleCaptureTable,
    handleCaptureGroupSheet
  };
}; 